    width: 748px;
    height: 748px;
    left: 586px;
    top: 152px;

    background: linear-gradient(
        135.18deg,
        #431153 24.92%,
        #3a3363 36.03%,
        #215e79 50.26%,
        #20a5b4 65.34%,
        #0cbaba 75.08%
    );
    border-radius: 6px;
    line-height: 0px;
}
.grid__group {
    padding-top: 24px;
    padding-left: 24px;
}

.toolbar {
    position: relative;
    width: 100%;
    height: 110px;
    left: 0px;
    top: 0px;

    background: linear-gradient(
        90.18deg,
        #370237 0%,
        #21617b 51.1%,
        #0cbaba 100%
    );
}
.toolbar__title {
    position: absolute;
    width: 785px;
    height: 54px;
    left: 32px;
    top: 18px;

    font-family: Saira;
    font-style: normal;
    font-weight: normal;
    font-size: 48px;
    line-height: 76px;

    color: #ffffff;
}
.toolbar__icon {
    position: absolute;
    left: 752px;
    top: 32px;
}
.toolbar__icon__square {
    position: absolute;
    width: 22px;
    height: 22px;

    background: #ffffff;
    border-radius: 2px;
}

.toggle-button {
    position: absolute;
    top: 36px;
    right: 40px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    height: 38px;
    width: 36px;

    background: transparent;
    border: none;
    cursor: pointer;
}
.toggle-button:focus {
    outline: none;
}
.toggle-button__line {
    width: 44px;
    height: 5px;
    background: white;
    border-radius: 4px;
}

.drawer__window {
    position: absolute;
    top: 0px;
    right: 0px;
    width: 450px;
    height: 100%;

    box-shadow: 2px 0px 5px black;
    background-color: white;
    z-index: 100;

    transform: translateX(100%);
    transition: transform 0.3s ease-out;
}
.drawer__window[open] {
    transform: translateX(0%);
}
.drawer__backdrop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    background: rgba(0, 0, 0, 0.7);
    border: none;
    z-index: 50;
}
.drawer__backdrop:focus {
    outline: none;
}
.drawer__window__title {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 166px;

    background: linear-gradient(
        96.16deg,
        #4d004d 18.32%,
        #21617b 56.2%,
        #0cbaba 78.84%
    );
}
.drawer__window__title div {
    position: absolute;
    top: 46px;
    left: 120px;

    font-family: Saira;
    font-style: normal;
    font-weight: normal;
    font-size: 48px;
    line-height: 76px;

    color: #ffffff;
}
.drawer__window__rule {
    position: absolute;

    font-family: Saira;
    font-style: normal;
    font-weight: normal;
    font-size: 48px;
    line-height: 76px;

    color: #2e2e2e;
}
.drawer__window__pattern {
    position: absolute;
    top: 208px;
    left: 52px;
    width: 174px;
}
.drawer__window__button {
    position: absolute;
    top: 208px;
    right: 52px;
    width: 58px;
}
.drawer__window__block--live {
    margin-bottom: 12px;
    display: inline-block;
    background-image: url(data:image/png;base64,);
    width: 58px;
    height: 58px;
}
.drawer__window__block--dead {
    margin-bottom: 12px;
    display: inline-block;
    background-image: url(data:image/png;base64,);
    width: 58px;
    height: 58px;
}
.drawer__window__line {
    position: absolute;
    width: 0px;
    height: 260px;
    left: 280px;

    border: 3px solid #2e2e2e;
    border-radius: 5px;
}
.drawer__window__arrow {
    position: absolute;
    left: 275px;
    top: 480px;

    width: 0;
    height: 0;
    border-top: 20px solid transparent;
    border-bottom: 20px solid transparent;

    border-left: 20px solid #2e2e2e;
}
.drawer__window__container input {
    cursor: pointer;
    display: none;
}
.drawer__window__container .checkmark {
    position: relative;
    width: 58px;
    height: 58px;

    margin-bottom: 12px;
    display: inline-block;
    background-image: url(data:image/png;base64,);
}
.drawer__window__container input:checked + .checkmark {
    position: relative;
    width: 58px;
    height: 58px;

    margin-bottom: 12px;
    display: inline-block;
    background-image: url(data:image/png;base64,);

    -webkit-animation: pulse-white 1s;

            animation: pulse-white 1s;
}
.drawer__window__container input:not(:checked) + .checkmark {
    -webkit-animation: pulse-black 1s;
            animation: pulse-black 1s;
}
@-webkit-keyframes pulse-black {
    0% {
        box-shadow: 0 0 0 0 rgba(0, 0, 0, 1);
    }

    70% {
        box-shadow: 0 0 0 6px rgba(0, 0, 0, 0);
    }

    100% {
        box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
}
@keyframes pulse-black {
    0% {
        box-shadow: 0 0 0 0 rgba(0, 0, 0, 1);
    }

    70% {
        box-shadow: 0 0 0 6px rgba(0, 0, 0, 0);
    }

    100% {
        box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
}
@-webkit-keyframes pulse-white {
    0% {
        box-shadow: 0 0 0 0 rgba(50, 50, 50, 1);
    }

    70% {
        box-shadow: 0 0 0 6px rgba(0, 0, 0, 0);
    }

    100% {
        box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
}
@keyframes pulse-white {
    0% {
        box-shadow: 0 0 0 0 rgba(50, 50, 50, 1);
    }

    70% {
        box-shadow: 0 0 0 6px rgba(0, 0, 0, 0);
    }

    100% {
        box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
}

.button {
    position: absolute;
    left: 1440px;
    top: 192px;
}
.button button {
    position: absolute;
    width: 88px;
    height: 88px;

    border: none;
    cursor: pointer;
}
.button button:focus {
    outline: none;
}

.button--start {
    position: absolute;
    left: 0px;
    top: 0px;

    background-image: url(/Elementary-Cellular-Automaton/static/media/button_start.bcd64ce6.svg);
}
.button--start[pulse="1"] {
    -webkit-animation: pulse-start 1s, flinch 0.4s;
            animation: pulse-start 1s, flinch 0.4s;
}
.button--start__label {
    position: absolute;
    left: 120px;
    top: 2px;

    font-family: Saira;
    font-style: normal;
    font-weight: normal;
    font-size: 48px;
    line-height: 76px;

    color: #2e2e2e;
}
@-webkit-keyframes pulse-start {
    0% {
        box-shadow: 0 0 0 0 rgba(120, 255, 190, 1);
    }
    70% {
        box-shadow: 0 0 0 6px rgba(120, 255, 190, 0);
    }
    100% {
        box-shadow: 0 0 0 0 rgba(120, 255, 190, 0);
    }
}
@keyframes pulse-start {
    0% {
        box-shadow: 0 0 0 0 rgba(120, 255, 190, 1);
    }
    70% {
        box-shadow: 0 0 0 6px rgba(120, 255, 190, 0);
    }
    100% {
        box-shadow: 0 0 0 0 rgba(120, 255, 190, 0);
    }
}
@-webkit-keyframes flinch {
    0% {
        transform: scale(0.98);
    }
    70% {
        transform: scale(1);
    }
    90% {
        transform: scale(0.98);
    }
    100% {
        transform: scale(0.99);
    }
}
@keyframes flinch {
    0% {
        transform: scale(0.98);
    }
    70% {
        transform: scale(1);
    }
    90% {
        transform: scale(0.98);
    }
    100% {
        transform: scale(0.99);
    }
}
.button--reset {
