}
@keyframes pulse-reset {
    0% {
        box-shadow: 0 0 0 0 rgba(255, 114, 114, 1);
    }

    70% {
        box-shadow: 0 0 0 6px rgba(255, 114, 114, 0);
    }

    100% {
        box-shadow: 0 0 0 0 rgba(255, 114, 114, 0);
    }
}

